import React from "react"
import { Link } from "gatsby"

import Layout from "../../components/layout"
import "./daily-notes.scss";
const allNotes = require("./notes.json");

let column = 7;
let columnArray = [];
let currentMonth = new Date().getMonth() + 1;

class DailyNotes extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      innerWidthKey: 0
    };
  }

  setColumnArray() {
    columnArray = [];
    for (let index = 0; index < column; index++) {
      let c = [];
      columnArray.push(c)
    }

    const sort = allNotes.sort(function () {
      return .5 - Math.random();
    })

    sort.forEach((item, key) => {
      let index = (key + 1) / column;
      index = key + 1 > column ?
        ((key + 1) % column) :
        key + 1;
      index = (index === column || index === 0) ?
        column - 1 :
        index - 1;
      columnArray[index].push(item);
    });
    this.setState({
      innerWidthKey: this.innerWidthKey + 1
    })
  }

  componentWillMount() {
    this.handleDailyNotesResize();
    if (typeof window !== 'undefined') {
      window.addEventListener('resize', this.handleDailyNotesResize)
    }
  }

  componentWillUnmount() {
    if (typeof window !== 'undefined') {
      window.removeEventListener('resize', this.handleDailyNotesResize)
    }
  }

  handleDailyNotesResize = () => {
    if (typeof window !== 'undefined') {
      if (window.innerWidth <= 200) {
        column = 1;
      }
      else if (200 < window.innerWidth && window.innerWidth <= 350) {
        column = 2;
      }
      else if (350 < window.innerWidth && window.innerWidth <= 500) {
        column = 3;
      }
      else if (500 < window.innerWidth && window.innerWidth <= 650) {
        column = 4;
      }
      else if (650 < window.innerWidth && window.innerWidth <= 800) {
        column = 5;
      }
      else if (800 < window.innerWidth && window.innerWidth <= 900) {
        column = 6;
      } else if (window.innerWidth > 2000) {
        column = 8;
      } else if (window.innerWidth > 2600) {
        column = 10;
      } else {
        column = 7;
      }
    }
    this.setColumnArray();
  }

  render() {
    return (
      <Layout>
          <div
            className="aphorism"
          >
            人生如蜡烛一样，从顶燃到底，一直都是光明的。
          </div>
        <div
          className="notes-wrap"
          key={this.innerWidthKey}
        >


          {
            columnArray.map((item, key) => {
              let marginHorizontal = `0.1rem`;
              let marginPortrait = `0.25rem`;
              return (
                <div
                  key={key}
                  className="column"
                  style={{
                    width: `calc(100% / ${column})`,
                    margin: `${marginHorizontal} ${marginPortrait}`
                  }}
                >
                  {
                    item.map((item, key) => {
                      let pathArray = item.path.split("/");
                      let itemMonth = Number(pathArray[pathArray.length - 1]);
                      let notesMargin = `0.2rem 0`;
                      let boderWidth = `100%`;
                      let boderHeight = `${Math.random() * 10 + 10}rem`;
                      return (
                        item.open ?
                          <Link
                            key={key}
                            to={item.path}
                            className={`notes nts-${item.subordinate} open ${itemMonth === currentMonth ? "animation" : ""}`}
                            style={{
                              width: boderWidth,
                              height: boderHeight,
                              margin: `${notesMargin}`,
                            }}
                          >
                            <div
                              className="year"
                              title={`${item.subordinate}年`}
                            >
                              {`${item.subordinate}`.slice(2)}
                            </div>
                            <div
                              className="month"
                            >{item.label}</div>
                            <div
                              className="title"
                              title={item.title}
                            >
                              {item.title}
                            </div>
                          </Link> :
                          <div
                            key={key}
                            to={item.path}
                            className={`notes nts-${item.subordinate} disabled`}
                            style={{
                              width: boderWidth,
                              height: boderHeight,
                              margin: `${notesMargin}`
                            }}
                          >
                            <div
                              className="year"
                            >
                              {`${item.subordinate}`.slice(2)}
                            </div>
                            <div
                              className="month"
                            >{item.label}</div>
                            <div
                              className="title"
                              title={item.title}
                            >
                              {item.title}
                            </div>
                          </div>
                      )
                    })
                  }
                </div>
              )
            })
          }
        </div>
      </Layout>
    );
  }
}

export default DailyNotes
